      <v-col
        cols="12"
        md="8"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-vuetify"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('服务员营业额') }}
            </div>
          </template>
          <v-divider class="mt-3"/>
          <v-data-table
            :fixed-header="true"
            :headers="staffHeaders"
            :items="staffArr"
            :items-per-page="10"
            :loading="loading"
            :search.sync="search"
            multi-sort
          >
            <template v-slot:item.paymentStatus="{ item }">
              <v-checkbox
                :readonly="true"
                :input-value="item.paymentStatus==='1'"
              />
            </template>
          </v-data-table>
        </base-material-card>
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-calendar-account"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('订单统计') }}
            </div>
          </template>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            class="ml-auto"
            hide-details
            :label="$t('Search')"
            single-line
            style="max-width: 250px;"
          />
          <v-divider class="mt-3"/>
          <v-data-table
            :fixed-header="true"
            :headers="headers"
            :items="desserts"
            :items-per-page="10"
            :loading="loading"
            :search.sync="search"
            multi-sort
          >
            <template v-slot:item.paymentStatus="{ item }">
              <v-checkbox
                :readonly="true"
                :input-value="item.paymentStatus==='1'"
                disabled
              />
            </template>
            <template v-slot:item.action="{ item }">
              <v-icon
                class="mr-2"
                small
                @click="goNext(item)"
              >
                mdi-arrow-right
              </v-icon>
            </template>
            <template v-slot:no-data>
              <v-btn
                color="primary"
                @click="loadDetail"
              >
                Reset
              </v-btn>
            </template>
          </v-data-table>
        </base-material-card>
      </v-col>
      <v-col
        cols="12"
        md="4"
      >
        <v-card
          max-width="600"
          class="mx-auto"
        >
          <v-toolbar
            color="light-blue"
            dark
          >
            <v-toolbar-title>{{ $t('日结单') }}</v-toolbar-title>
            <v-spacer/>
          </v-toolbar>

          <v-list
            two-line
            subheader
          >
            <v-list-item
              v-for="item in infoList"
              :key="item.title"
            >
              <v-list-item-avatar>
                <v-icon
                  v-text="item.icon"
                />
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title
                  class="d-flex justify-space-between font-weight-black text-h4"
                  v-text="item.title"
                />
              </v-list-item-content>

              <v-list-item-action>
                <v-list-item-action-text
                  class="text-h3"
                  v-text="item.action"
                />
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

  import { getTodayBills } from '../../components/Utlis/api'

  export default {
    data: function () {
      return ({
        loading: true,
        infoList: [],
        BillData: {},
        headers: [
          {
            text: 'Id',
            align: 'start',
            sortable: false,
            value: 'orderId',
          },
          {
            text: this.$i18n.t('servantName'),
            value: 'servantName',
          },
          {
            text: this.$i18n.t('time'),
            value: 'eatTime',
          },
          {
            text: this.$i18n.t('price'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('paymentStatus'),
            value: 'paymentStatus',
          },
          {
            text: this.$i18n.t('action'),
            value: 'action',
            sortable: false,
          },
        ],
        search: '',
        desserts: [],
        staffHeaders: [
          {
            text: this.$i18n.t('servantName'),
            value: 'name',
          },
          {
            text: this.$i18n.t('income'),
            value: 'income',
          },
          {
            text: this.$i18n.t('billCount'),
            value: 'billCount',
          },
        ],
        staffArr: [],
      })
    },
    created () {
      this.loadDetail()
    },
    methods: {
      loadDetail () {
        getTodayBills().then(res => {
          this.BillData = res
          this.infoList = []
          this.infoList.push({
            title: this.$i18n.t('今日营业总额'),
            action: this.BillData.totalIncome + '€',
            icon: 'mdi-bulletin-board',
          })
          this.infoList.push({
            title: this.$i18n.t('总单数'),
            action: this.BillData.totalCount,
            icon: 'mdi-poll',
          })
          this.infoList.push({
            title: this.$i18n.t('酒水营业额'),
            action: this.BillData.drinkInfo + '€',
            icon: 'mdi-coffee',
          })
          this.infoList.push({
            title: this.$i18n.t('食品营业额'),
            action: this.BillData.foodInfo + '€',
            icon: 'mdi-noodles',
          })
          if (this.BillData.takeawayAmount) {
            this.infoList.push({
              title: this.$i18n.t('外卖总销售额'),
              action: this.BillData.takeawayAmount + '€',
              icon: 'mdi-tractor',
            })
          }
          this.desserts = this.BillData.todayBill
          this.staffArr = this.BillData.servantRank
          this.loading = false
        })
      },
      goNext (item) {
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
    },
  }
</script>
